import { TextField } from '@material-ui/core'
import { Grid } from '@material-ui/core'
import React from 'react'
import { useState } from 'react'
import styled from 'styled-components'
import CheckIcon from '@material-ui/icons/Check';
import { Button } from '@material-ui/core'
import { db } from '../firebase'
import firebase from 'firebase'
import check from '../../images/checkmark.gif'
import { Link } from 'react-router-dom'
import { useRef } from 'react'
import { useDispatch } from 'react-redux'
import { intoCart } from '../../features/userSlice'

export default function Shipmentpage({totalPrice, prod, user}) {
    const [name, setName] = useState('')
    const [lname, setLname] = useState('')
    const [phone, setPhone] = useState('')
    const [email, setEmail] = useState('')
    const [address, setAddress] = useState('')
    const [zip, setZip] = useState('')
    const [city, setCity] = useState('')
    const [county, setCounty] = useState('')
    const popRef = useRef()
    const dispatch = useDispatch()

    const handleSubmit = e => {
        e.preventDefault()

        db.collection("sales").add({
            name,
            lname,
            phone,
            email,
            address,
            zip,
            city,
            county,
            total: Number(totalPrice),
            items: prod.map(item => ({
                product: item.product,
                price: Number(item.price),
                amount: Number(item.amount)
            })),
            timestamp: firebase.firestore.FieldValue.serverTimestamp(),
            delivered: false
        })

        dispatch(intoCart([]))
        db.collection('users').doc(user.email).collection('cart').onSnapshot((snapshot) => {
            snapshot.docs.forEach((doc) => {
                db.collection('users').doc(user.email).collection('cart').doc(doc.id).delete()
            })
        })

        popRef.current.style.visibility = "visible";
    }

    return (
        <Container>
            <Wrapper>
                <Title>Acas  Finalizare comand</Title>
                <Section>
                   <Form onSubmit={handleSubmit}>
                       <SubTitle>Informaii Facturare i Livrare</SubTitle>
                       <MyGrid container spacing={1}>
                            <Grid item xs={12} sm={6}>
                                <TextField
                                    label="Nume"
                                    required
                                    type="text"
                                    name="lname"
                                    fullWidth
                                    color="secondary"
                                    value={lname}
                                    onChange={(e) => setLname(e.target.value)}
                                />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <TextField
                                    label="Prenume"
                                    required
                                    type="text"
                                    name="fname"
                                    fullWidth
                                    color="secondary"
                                    value={name}
                                    onChange={(e) => setName(e.target.value)}
                                />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <TextField
                                    label="Numr de Telefon"
                                    required
                                    type="tel"
                                    name="phone"
                                    fullWidth
                                    color="secondary"
                                    value={phone}
                                    onChange={(e) => setPhone(e.target.value)}
                                />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <TextField
                                    label="Adresa de e-mail"
                                    type="email"
                                    name="e-mail"
                                    fullWidth
                                    color="secondary"
                                    value={email}
                                    onChange={(e) => setEmail(e.target.value)}
                                />
                            </Grid>
                            <Grid item xs={12} sm={10}>
                                <TextField
                                    label="Adresa"
                                    required
                                    type="text"
                                    name="address"
                                    fullWidth
                                    color="secondary"
                                    value={address}
                                    onChange={(e) => setAddress(e.target.value)}
                                />
                            </Grid>
                            <Grid item xs={12} sm={2}>
                                <TextField
                                    label="Cod Potal"
                                    required
                                    type="number"
                                    name="zip code"
                                    fullWidth
                                    color="secondary"
                                    value={zip}
                                    onChange={(e) => setZip(e.target.value)}
                                />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <TextField
                                    label="Localitatea"
                                    required
                                    type="text"
                                    name="city"
                                    fullWidth
                                    color="secondary"
                                    value={city}
                                    onChange={(e) => setCity(e.target.value)}
                                />
                            </Grid>
                            <Grid item xs={12} sm={6}>
                                <TextField
                                    label="Judeul"
                                    required
                                    type="text"
                                    name="county"
                                    fullWidth
                                    color="secondary"
                                    value={county}
                                    onChange={(e) => setCounty(e.target.value)}
                                />
                            </Grid>
                        </MyGrid>
                        <Products>{prod.map((item, i) => (
                            <p key={i}><u>{item.product}</u> - <b>{item.price}</b> RON x{item.amount}</p>
                        ))}</Products>
                        <p>+20 RON Cost livrare (GRATIS dac suma total depete 400 RON)</p>
                        <Suma>Suma Total: <b>{(totalPrice + (totalPrice > 400 || totalPrice == 0 ? 0 : 20)).toLocaleString('ro')}</b> RON</Suma>
                        <Button 
                            type="submit"
                            variant="contained"
                            color="default"
                            endIcon={<CheckIcon />}
                        >
                            Finalizeaz comanda
                        </Button>
                   </Form>

                   <Popup ref={popRef}>
                       <Img src={check} alt="" />
                       <Link to="">Comand trimis</Link>
                   </Popup>
                </Section>
            </Wrapper>
        </Container>
    )
}



const Container = styled.article`
    width: 100%;
    background-image: linear-gradient(66deg, transparent 0%, transparent 24%,rgba(195, 195, 195,0.04) 24%, rgba(195, 195, 195,0.04) 80%,transparent 80%, transparent 100%),linear-gradient(153deg, transparent 0%, transparent 29%,rgba(195, 195, 195,0.04) 29%, rgba(195, 195, 195,0.04) 45%,transparent 45%, transparent 100%),linear-gradient(199deg, transparent 0%, transparent 32%,rgba(195, 195, 195,0.04) 32%, rgba(195, 195, 195,0.04) 59%,transparent 59%, transparent 100%),linear-gradient(277deg, transparent 0%, transparent 56%,rgba(195, 195, 195,0.04) 56%, rgba(195, 195, 195,0.04) 57%,transparent 57%, transparent 100%),linear-gradient(90deg, rgb(0,0,0),rgb(0,0,0));
`
const Wrapper = styled.div`
    background-image: linear-gradient(305deg, rgba(254, 254, 254,0.02) 0%, rgba(254, 254, 254,0.02) 1%,transparent 1%, transparent 50%,rgba(220, 220, 220,0.02) 50%, rgba(220, 220, 220,0.02) 64%,rgba(249, 249, 249,0.02) 64%, rgba(249, 249, 249,0.02) 100%),linear-gradient(38deg, rgba(70, 70, 70,0.02) 0%, rgba(70, 70, 70,0.02) 35%,transparent 35%, transparent 62%,rgba(152, 152, 152,0.02) 62%, rgba(152, 152, 152,0.02) 74%,rgba(99, 99, 99,0.02) 74%, rgba(99, 99, 99,0.02) 100%),linear-gradient(337deg, rgba(124, 124, 124,0.02) 0%, rgba(124, 124, 124,0.02) 45%,transparent 45%, transparent 55%,rgba(34, 34, 34,0.02) 55%, rgba(34, 34, 34,0.02) 72%,rgba(189, 189, 189,0.02) 72%, rgba(189, 189, 189,0.02) 100%),linear-gradient(92deg, rgba(239, 239, 239,0.02) 0%, rgba(239, 239, 239,0.02) 12%,transparent 12%, transparent 22%,rgba(204, 204, 204,0.02) 22%, rgba(204, 204, 204,0.02) 51%,rgba(70, 70, 70,0.02) 51%, rgba(70, 70, 70,0.02) 100%),linear-gradient(90deg, rgb(255,255,255),rgb(255,255,255));
    max-width: 1000px;
    margin: 0 auto;
    min-height: 100vh;
`
const Section = styled.div`
    padding: 10px;
`
const Title = styled.h1`
    width: 100%;
    color: #292929;
    padding: 100px 0 20px 100px;
    background-color: #00000021;
    font-size: clamp(1rem, 5vw, 2rem);
    font-family: 'Big Shoulders Display', cursive;
    border-bottom: 1px solid lightgray;
`
const Form = styled.form`
    margin: 50px auto;
    button {
        display: block;
        width: 300px;
        margin: 50px auto !important;
        color: white;
        background-color: var(--c1);
        height: 40px;
        span {
            display: inline-flex;
        }
        &:hover {
            background-color: var(--c1h);
        }
    }
`
const SubTitle = styled.h3`
    color: #292929;
`
const MyGrid = styled(Grid)`

`
const Suma = styled.p`
    margin-top: 10px;
    >b {
        color: var(--c1);
    }
`
const Products = styled.div`
    display: flex;
    flex-direction: column;
    margin-top: 10px;
`
const Popup = styled.div`
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    display: grid;
    place-content: center;
    background-color: #000000b5;
    transition: all 1s linear;
    visibility: hidden;
    >a {
        text-decoration: none;
        background-color: #0a5500;
        padding: 20px;
        color: #76e660;
        text-align: center;
        border-bottom-right-radius: 10px;
        border-bottom-left-radius: 10px;
        &:hover {
            background-color: #0e3a00;
        }
    }
`
const Img = styled.img`
    width: 300px;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
`
